const TARGET_DIV = "insultSpot";
const VICTIM_NAME = "Sean";

function randomMe(low = 1, high = 100){
    let randomNum = Math.floor(Math.random() * (high - low + 1)) + low;
    return randomNum;
}

function insultMe(victim) {

    // random noun generation
    // get random number for noun
    let randomNum = randomMe(1,5);

    // == != >= <= < >
    // === !==
    let noun = "";
    if (randomNum == 5) {
        noun = "jerk";
    } else if (randomNum == 4) {
        noun = "dork";
    } else if (randomNum == 3) {
        noun = "simpleton";
    } else if (randomNum == 2) {
        noun = "geek";
    } else {
        noun = "doofus";
    }

    //console.log("noun: " + noun);

    // random adjective generation
    randomNum = randomMe(1,5);
    let adjective = "";
    if (randomNum == 5) {
        adjective = "goofy";
    } else if (randomNum == 4) {
        adjective = "dorky";
    } else if (randomNum == 3) {
        adjective = "bumbling";
    } else if (randomNum == 2) {
        adjective = "awkward";
    } else {
        adjective = "bewildering";
    }

    // APPROACH I
    //let insult = "\"" + victim + " is a " + adjective + " " + noun + "!\"";
    // APPROACH II
    let insult = `"${victim} is a ${adjective} ${noun}!"`;

    return insult;
}

